<template>
  <div id="index">
    <div class="row menu">
      <div class="col">
        <a :class="{active : topSongs}" href="#" @click="fetchTop()">Top 20</a>
      </div>
      <div class="col">
        <a :class="{active : latestSongs}" href="#" @click="fetchLatest()">Latest</a>
      </div>
    </div>

    <div class="content-holder">
      <div class="container">
        <song-row @click="doSomething()"></song-row>
        <song-row></song-row>
        <song-row></song-row>
        <song-row></song-row>
        <song-row></song-row>
        <song-row></song-row>
      </div>
    </div>
  </div>
</template>
<script>
import Songrow from './Songrow.vue';

export default {
  name: "index",
  data: () => ({
    topSongs : true,
    latestSongs : false
  }),
  components: {
    'song-row' : Songrow
  },
  methods: {
    fetchTop(event) {
      this.topSongs = true;
      this.latestSongs = false;
    },
    fetchLatest(event){
      this.topSongs = false;
      this.latestSongs = true;
    },
    doSomething(event){
      alert("hello");
      //event.target.height = '300px';
    }
  }
}
</script>
<style lang="scss" scoped>
.content-holder{
  margin-top: 30px;
  background-color: #fff;
  box-shadow: 0 1px 7px rgba(0, 0, 0, 0.04);
}
.menu{

  background-color: #fff;
  text-align: center;
  box-shadow: 0 5px 5px #efefef;

  a{
    padding: 10px 25px;
    text-transform: uppercase;
    display: inline-block;
    color: #b5b5b5;

    &.active{
      color: #000;
      box-shadow: inset 0 -2px 0 #4286f5;
    }
  }
}
</style>



