<template>

    <div id="Songrow" class="row align-items-center">
      <div class="col-1 "><span class="sno">2</span></div>
      <div class="col-2">
        <img class="coverImage" :src="imgUrl" height="50px" width="50px" >
      </div>
      <div class="col">
        <h2>{{ songName }}</h2>
        <p>{{ artist }}</p>
      </div>

    </div>

</template>
<script>
export default {
  name: "Songrow",
  data: () => ({
    imgUrl : 'http://images.indianexpress.com/2015/05/sunnyleone-bikini1.jpg?w=820',
    songName : 'Saree Wali Girl',
    artist : 'Girik Aman'
  })
}
</script>
<style lang="scss" scoped>
#Songrow{
  padding: 5px 0;
  border-bottom: solid 1px rgba(0,0,0,0.1);

  .sno{
    color: rgba(0,0,0,0.5);
    background-color: rgba(0,0,0,0.08);
    border-radius: 50%;
    font-size: 12px;
    line-height: 20px;
    width: 20px; height: 20px;
    display: inline-block; text-align: center;
  }
  .coverImage{
    border-radius: 50px;
  }
  h2{
    margin: 0; padding: 0;
    font-size: 18px;
    font-weight: 100;
  }
  p{
    margin: 0; padding: 0;
    font-size: 14px;
    color: rgba(0,0,0,0.4);
  }
}
</style>



